import React, { FC, useState, Fragment } from 'react';
import Grid from '@material-ui/core/Grid';
import Pagination from '@material-ui/lab/Pagination';
import styled from '@theme';
import { default as ArticleCard } from '@src/components/ArticleCard';

const StyledWrapper = styled.div`
    .pagination {
        &-top {
            margin-bottom: ${(props) => props.theme.spacing(10)}px;
        }

        &-bottom {
            margin-top: ${(props) => props.theme.spacing(10)}px;
        }
    }
`;

type IProps = {
    limit?: number;
    enablePagination?: boolean;
    articleList: any[];
    showAuthor: boolean;
};

const ArticleMaker: FC<IProps> = ({ articleList, limit, enablePagination, showAuthor }) => {
    const [page, setPage] = useState<number>(1);
    const total: number = articleList.length;
    const count: number = Math.ceil(total / limit);

    const mappingList = (): any[] => {
        if (!enablePagination && !limit) {
            return articleList;
        }

        if (!enablePagination && limit) {
            const result = articleList.slice(0, limit);
            return result;
        }

        if (enablePagination && limit) {
            const offset = (page - 1) * limit;
            const take = page * limit;
            const result = articleList.slice(offset, take);
            return result;
        }
    };

    const handleChangePage = (event: React.ChangeEvent<unknown>, value: number) => {
        setPage(value);
    };

    return (
        <StyledWrapper>
            <Grid item xs={12}>
                <Grid container justify="center" spacing={3}>
                    {mappingList()?.map((edge) => (
                        <Grid key={edge.node.id} item>
                            <ArticleCard articleData={edge.node} showAuthor={showAuthor} />
                        </Grid>
                    ))}
                </Grid>
            </Grid>
            {enablePagination && (
                <Grid container justify="flex-end" className="pagination-bottom">
                    <Pagination
                        count={count}
                        page={page}
                        onChange={handleChangePage}
                        color="primary"
                        showFirstButton
                        showLastButton
                    />
                </Grid>
            )}
        </StyledWrapper>
    );
};

export default ArticleMaker;
